"use client";
import { useState, useEffect } from "react";

export default function AdminPage() {
  const [content, setContent] = useState("");
  const [url, setUrl] = useState("");
  const [status, setStatus] = useState("");
  const [entries, setEntries] = useState([]);

  async function loadEntries() {
    const res = await fetch("/api/admin/chunks");
    const data = await res.json();
    if (res.ok) setEntries(data.items || []);
  }

  useEffect(() => {
    loadEntries();
  }, []);

  async function addEntry() {
    setStatus("");
    if (!content.trim()) return;
    const res = await fetch("/api/admin/chunks", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ content, url }),
    });
    const data = await res.json();
    if (res.ok) {
      setStatus("Saved!");
      setContent("");
      setUrl("");
      loadEntries();
    } else {
      setStatus(data.error || "Error saving");
    }
  }

  return (
    <main className="max-w-2xl mx-auto space-y-4">
      <h1 className="text-2xl font-bold">Add Info!</h1>

      {/* Single big text area */}
      <textarea
        className="w-full border rounded px-3 py-2 min-h-[120px]"
        placeholder="Paste info, event details, policy, etcâ€¦"
        value={content}
        onChange={(e) => setContent(e.target.value)}
      />

      {/* URL field below */}
      <input
        className="w-full border rounded px-3 py-2"
        placeholder="Optional URL"
        value={url}
        onChange={(e) => setUrl(e.target.value)}
      />

      <button
        onClick={addEntry}
        className="px-4 py-2 rounded bg-[#7a1f3d] text-white hover:opacity-90"
      >
        Save
      </button>
      {status && <p className="text-sm">{status}</p>}

      {/* Entries list */}
      <div className="space-y-2">
        {entries.length === 0 && <p className="text-sm">No results.</p>}
        {entries.map((e) => (
          <div key={e.id} className="border rounded p-2 bg-white/70">
            <div className="font-medium">{e.content}</div>
            {e.url && (
              <a
                href={e.url}
                target="_blank"
                className="text-blue-600 underline text-sm"
              >
                {e.url}
              </a>
            )}
          </div>
        ))}
      </div>
    </main>
  );
}
